#articleNav {
  width: 100%;
  height: 41px;
  line-height: 35px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #c4e2f4; }
  #articleNav a {
    color: white;
    margin-right: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    text-shadow: 1px 1px 3px #9376f8;
    text-decoration: none; }
    #articleNav a:hover {
      color: #ffffff;
      border-bottom: 3px solid #f0a5b4; }

main {
  width: 1200px;
  margin: 0 auto; }

.video-up {
  padding-top: 15px;
  padding-bottom: 15px; }
  .video-up .video-up-name {
    font-size: 24px;
    color: #e87a90; }

.video-title {
  margin-top: 15px;
  margin-bottom: 0; }

.video-play {
  width: 100%; }
  .video-play lfx-player {
    width: 854px;
    height: 480px;
    background-color: #000000; }
    .video-play lfx-player > .lfx-player {
      width: 100%;
      height: 100%; }
    .video-play lfx-player video {
      width: 100%;
      height: 100%; }
    .lfx-player {
      cursor: pointer; }
      .lfx-player__controlbar {
        display: flex;
        flex-flow: row;
        width: 370px;
        height: 40px;
        transition: opacity 0.3s ease; }
      .lfx-player__volume {
        display: flex;
        align-items: center;
        position: absolute;
        top: 50%;
        left: 20px;
        transform: translateY(-50%); }
        .lfx-player__volume #videovolume + .rangeSlider {
          margin-left: 10px;
          width: 90px; }
      .lfx-player__toggle {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%); }
      .lfx-player__list {
        position: absolute;
        top: 50%;
        right: 45px;
        transform: translateY(-50%); }
      .lfx-player__full {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translateY(-50%); }
      .lfx-player__progress {
        transition: opacity 0.3s ease; }
        .lfx-player__progress #videorange + .rangeSlider .rangeSlider__fill {
          z-index: 2; }
        .lfx-player__progress #videoprogress + .rangeSlider {
          top: -3px; }
          .lfx-player__progress #videoprogress + .rangeSlider .rangeSlider__handle {
            display: none; }
          .lfx-player__progress #videoprogress + .rangeSlider .rangeSlider__fill {
            z-index: 1;
            background: #a6bced; }
  .video-play lfx-player.mouseenter > .lfx-player {
    cursor: pointer; }
    .video-play lfx-player.mouseenter > .lfx-player .lfx-player__controlbar {
      opacity: 1; }
    .video-play lfx-player.mouseenter > .lfx-player .lfx-player__progress {
      opacity: 1; }
  .video-play lfx-player.mouseleave > .lfx-player {
    cursor: pointer; }
    .video-play lfx-player.mouseleave > .lfx-player .lfx-player__controlbar {
      opacity: 0; }
    .video-play lfx-player.mouseleave > .lfx-player .lfx-player__progress {
      opacity: 0; }

.video-danmu {
  flex-grow: 0;
  flex-basis: 331px; }


